div.streamer-component {

    --preview-height: 360px;
    --preview-width: 640px;
    --logo-border-size : 2px;
    --component-border-size : 5px;
    --base-size : 48px;

    --adjusted-logo-border : calc(var(--logo-border-size) * var(--base-scale));
    --adjusted-border-size :  calc(var(--component-border-size) * var(--base-scale));
    --component-height : calc(var(--base-size) * var(--base-scale));
    --adjusted-preview-height : calc(var(--preview-height) * var(--base-scale));
    --adjusted-preview-width : calc(var(--preview-width) * var(--base-scale));
    --adjusted-font-size : calc(var(--component-height)/3.5);
    --open-animation-time : 0.3s;
    --posx : 0;
    --posy : 0;
    --opacity : 0;

    top: calc( var(--posy) * ( ( ( var(--base-scale) - 1) / 4) + 1) );
    left: var(--posx);
    opacity: var(--opacity);

    font-size: var(--adjusted-font-size);
    height: var(--component-height);
    width: calc(var(--component-height)*4);

    margin:10px;
    position: absolute;
    font-family: 'g4m3rZ';
    letter-spacing: 0.6px;
    padding: var(--adjusted-logo-border);
    background-color: #444;
    border: var(--adjusted-border-size) solid #333;
    border-color: #303030 #161616 #191919 #343434;
    background-image: linear-gradient(-45deg, #202020 , #303030);
    z-index: 5;
    overflow: hidden;
    box-shadow:
    0 0 0 5px rgba(22,22,22,0);
    transition: 
    box-shadow 0.2s ease-in-out,
    opacity 0.2s ease-in-out 0.5s,
    height var(--open-animation-time) ease-in-out,
    width var(--open-animation-time) ease-in-out,
    left var(--open-animation-time) ease-in-out,
    top var(--open-animation-time) ease-in-out,
    z-index 0.3s linear;
    line-height: normal;
}



div.streamer-component.detailedStream{
    height: var(--adjusted-preview-height);
    width: var( --adjusted-preview-width);
    z-index: 100;
    padding: 0;
}

div.streamer-component.detailedStream::after
{
 box-shadow: inset 0 0 calc(var(--adjusted-border-size)*2.5) calc(var(--adjusted-border-size)*0.75) #000000;
 content: '';
 display: block;
 height: 100%;
 position: absolute;
 top: 0;
 width: 100%;
}


@keyframes imageOpacity {
    100% {
        opacity: 1;
    }
}

div.streamer-component.detailedStream img.streamPreview {
    position: absolute;
    top: 0;
    left: 0;
    height: var(--adjusted-preview-height);
    width: var( --adjusted-preview-width);
    z-index: -1;
    box-shadow: -1px 2px 10px 3px rgba(0, 0, 0, 1) inset;
    
    opacity: 0;
    animation: imageOpacity 0.3s ease;
    animation-delay: 0.3s;
    animation-fill-mode:forwards;
}

div.streamer-component.live:hover {
    box-shadow:
    0 0 calc(var(--adjusted-border-size)*1.5) 1px rgb(62,249,65);
    
}

div.streamer-component.offline:hover {
    box-shadow:
    0 0 calc(var(--adjusted-border-size)*1.5) 1px rgb(150, 150, 150);
}

/* === Décale la fenêtre pour éviter l'overflow*/

div.streamer-component.detailedStream.rightClamp{
    left: calc(var(--posx) - 640px * var(--base-scale) + var(--component-height) * 4 + var(--adjusted-border-size));
    
}

div.streamer-component.detailedStream.bottomClamp{
    top: calc(var(--posy) - 360px * var(--base-scale) + var(--component-height) + var(--adjusted-border-size));
    
}

/* === Props === */

div.streamer-component div.streamProps {
    display: flex;
    flex-direction: row;  
    flex: 1;
    height: calc(var(--component-height) * 0.7 +  var(--adjusted-logo-border));
    margin-top: calc(var(--component-height) * 0.15);
    margin-left: calc(var(--adjusted-logo-border) * 0.05);
    transition: 
    margin-top 0.3s ease-in-out,
    margin-left 0.3s ease-in-out;
    padding-right: calc(var(--component-height) * 0.1);

}

div.streamer-component.detailedStream div.streamProps {
    display: inline-flex;
    margin: calc(1em + var(--component-height) * 0.05);
    height: calc(9px + var(--component-height) * 1);
    overflow: visible;
}

/* === Img === */

div.streamer-component img.logo {
    display: block;
    background-color: #555;
    border-radius: 50%;
   
    z-index: 10;
    box-shadow:
    0 0 0 var(--border-scale) white;

    height: calc(var(--component-height) * 0.7);
    width: calc(var(--component-height) * 0.7);
    min-width: calc(var(--component-height) * 0.7);
    border-width: var(--adjusted-logo-border);
    margin: calc((var(--component-height) * 0.15) + var(--adjusted-logo-border) * -1);
    margin-top:0;
    
    cursor: pointer;
    transition: all 0.3s ease ;
}

div.streamer-component.detailedStream img.logo {
    margin-left:0;
    margin-right:0;
    height: calc(10px + var(--component-height) * 1.1);
    width: calc(10px + var(--component-height) * 1.1);
    min-width: calc(var(--component-height) * 1.1);
}

div.streamer-component.offline img.logo {
    filter: grayscale(60%) contrast(75%);
}

div.streamer-component.live img.logo {
    box-shadow:
    0 0 0 calc(var(--border-scale)) rgb(62,249,65),
    0 0 calc(var(--adjusted-logo-border) * 2.5) calc(var(--adjusted-logo-border) * 1.5) rgb(62,249,65);

}

/* === logoSide === */

div.streamer-component div.logoSide {
    display: flex;
    flex-direction: column;
    flex:1;
    min-width: 0px;
    margin-left: calc(1px + var(--adjusted-logo-border));
    transition:
    margin-top 0.3s ease-out,
    padding-top 0.3s ease-out,
    padding-left 0.3s ease-in-out;
}

div.streamer-component.detailedStream div.logoSide {
    background-image: linear-gradient(-45deg, #222 , #333);
    border:  calc(1px + var(--adjusted-logo-border)) solid #333;
    border-color: #303030 #161616 #191919 #343434;
    margin-left: calc(var(--component-height) * 0.55 * -1);
    margin-top: calc(3px + var(--component-height) * 0.1);
    padding-top: calc(1px + var(--component-height) * 0.05);
    padding-bottom: calc(var(--component-height) * 0.05);
}

/* === spans === */

div.logoSide span {
    max-width: 100%;
    height: calc(1em + var(--component-height) * 0.5);
    transition:
    height 0.3s ease;
    display: block;
    z-index: 1;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space:nowrap;
    font-weight: normal;
    
}

div.logoSide span.name {
    font-size : calc( var(--adjusted-font-size) * 1.2);
    font-family: 'g4m3rZ_bold';
    padding-bottom: 3px;
    margin-top: -1px;
}

div.logoSide span.islive {
    font-style: italic;
    padding-bottom: 1px;
    color : #CCC;
}

div.streamer-component.offline span.islive {
    color : #AAA;
}

div.streamer-component.detailedStream span {
    height: calc(1em + var(--component-height) * 0.10);
    padding-left: calc(0.5em + var(--component-height) * 0.55);
    padding-right: 0.5em;
}

/* footer */

@keyframes footerOpacity {
    100% {
        opacity: 1;
    }
}

div.streamer-component.detailedStream div.streamFooter {
    position: absolute;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: 100%;
    grid-gap: 0;
    bottom: 0;
    left: 0;
    margin-bottom: -1px;
    width: 100%;
    height: 3em;
    background-image: linear-gradient(-45deg, rgba(20,20,20,0.8) , rgba(30,30,30,0.8));
    padding-left: 1em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    min-height: 0;
    min-width: 0;
    overflow: hidden;
    opacity: 0;
    backdrop-filter: blur(2px);

    animation: footerOpacity 0.2s linear;
    animation-delay: 0.3s;
    animation-fill-mode:forwards;
}

/* stream title */

div.streamer-component.detailedStream div.titleDiv{
    
    grid-column: 1/5;
    overflow: hidden;
    height: 100%;
    display: flex;
}

@keyframes moveSlideshow {
    0% {
        transform: translateY(0%);
    }
    20% {
        transform: translateY(var(--animation-size));
    }
    40% {
        transform: translateY(var(--animation-size));
    }
    60% {
        transform: translateY(0%);
    }
    100% {
        transform: translateY(0%);
    }
  }

div.streamer-component.detailedStream span.streamTitle.addScroll {
    --animation-size : 0%;

    animation: moveSlideshow 10s linear infinite;
    animation-delay: 2s;
}

div.streamer-component.detailedStream span.streamTitle{

    

    display: inline-block;
    padding: 0;
    overflow: visible;
    margin:auto;
    margin-left:0;
    margin-right:0;
    height:auto;
}

/* viewers */

div.streamer-component.detailedStream span.streamViewers{
    font-size: 1.15em;
    grid-column: 5;
    margin: auto;
    color: red;
    height: 2em;
    display: inline-flex;
    height: 100%;
    align-items: center;
    padding:0;
    vertical-align: middle;
}

div.streamer-component.detailedStream span.viewerCount {
    padding:0;
    margin: auto;
    font-weight: bold;
}

span.streamViewers svg {
    margin: auto;
    padding-bottom: 1px;
    height: 1.15em;
    width: 0.9em;
    margin-left:0.5em;
    filter: invert(11%) sepia(80%) saturate(5881%) hue-rotate(360deg) brightness(122%) contrast(119%);
}
