body {
    background-color: #101010;
    font-family: Verdana, Helvetica, sans-serif;
    color: white;
    min-width: 1024px;
    overflow-x: hidden;
    user-select: none;
    margin: 0;
}



div#root {
    min-width: 1024px;
}

nav {
    position: relative;
    display: flex;
    flex-direction: row;
    min-height: 40px;
    height: 50px;
    width: 100%;
    top: 0%;
    left:0%;
    background:#222222;
    z-index: 100;
}

h1.mainLogo {
    display: block;
    margin: auto;
    margin-left:2em;
    text-shadow: 0 0 5px rgb(62,249,65);
}

@font-face {
    font-family: "g4m3rZ";
    src: url("../fonts/Jura-Medium.ttf") format("truetype");
}

@font-face {
    font-family: "g4m3rZ_bold";
    src: url("../fonts/Jura-Bold.ttf") format("truetype");
}
  