div.searchDiv{
    margin:auto;
    margin-right: 50px;
    height: 60%;
    overflow: hidden;
    border: 3px solid #202020;
    /*border-color: #303030 #161616 #191919 #343434;*/
    background-color: #EEE;
    display: flex;
    flex-direction: row;
    padding-left: 5px;
}

div.searchDiv input.searchBox {
    height: 100%;
    border-radius: 0;
    padding-left: 5px;
    padding-right: 5px;
    font-family: "g4m3rZ_bold";
    font-size: 16px;
    outline: none;
    border : none;
}

div.streamer-component.found {
    box-shadow:
    0 0 20px 5px rgb(255,255,255);
    
}

div.searchDiv svg {
    display: block;
    margin-top:7px;
    margin-right: 5px;
    height: 16px;
    width: 16px;
}